
.p-button-plain {
    background-color: white !important;
    color: #2a2a2a !important;
    border-color: #2a2a2a !important;
}

.project-page-tags {
    background-color: rgb(237, 235, 235);
    color: black;
    border-radius: 4px;
    display: block;
    text-align: center;
    word-wrap: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    cursor: pointer;
    width: 100%;
    font-weight: 400;
    border: none;
    text-shadow: none;
    padding: 15px 0 16px;
    margin-top: 15px;
    font-size: 13px;
}